.footer {
	padding-top: 63px;
	padding-bottom: 32px;
	background: #3E53BB;
	
	@media (max-width: 540px) {
		padding-top: 25px;
		padding-bottom: 18px;
	}
	
	&_content {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-bottom: 85px;
		
		@media (max-width: 540px) {
			display: block;
			margin-bottom: 23px;
		}
	}
	
	&_main-block {
		width: 31%;
		
		@media (max-width: 540px) {
			width: 100%;
			padding-bottom: 29px;
			border-bottom: 1px solid #FFFFFF;
		}
	}
	
	&_logo {
		display: block;
		margin-bottom: 25px;
		
		svg {
			@media (max-width: 540px) {
				max-width: 224px;
			}
		}
		
		&:hover {
			cursor: pointer;
		}
	}
	
	&_adress {
		margin-bottom: 15px;
	}
	
	&_contacts-group {
		margin-bottom: 20px;
	}
	
	&_contacts {
		margin-bottom: 7px;
		
		svg {
			position: relative;
			top: 2px;
			margin-right: 8px;
		}
	}
	
	&_socials {
		display: flex;
		align-items: center;
		
		a {
			margin-right: 12px;
		}
	}
	
	&_column {
		display: block;
		
		@media (max-width: 540px) {
			padding: 20px 0;
			border-bottom: 1px solid #FFFFFF;
			
			&:last-child {
				border: none;
				padding-bottom: 0;
			}
		}
		
		.footer_text {
			margin-bottom: 22px;
			
			a {
				color: white;
			}
			
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	
	&_second-column {
		width: 16%;
		
		@media (max-width: 540px) {
			width: 100%;
		}
	}
	
	&_third-column {
		width: 26.7%;
		
		@media (max-width: 540px) {
			width: 100%;
		}
	}
	
	&_fourth-column {
		width: 26.3%;
		
		@media (max-width: 540px) {
			width: 100%;
		}
	}
	
	&_text {
		display: block;
		font-size: 18px;
		line-height: 22px;
		color: #FFFFFF;
		
		a:hover {
			cursor: pointer;
		}
	}
	
	&_copyright {
		font-size: 16px;
		line-height: 19px;
		color: $white;
		text-align: center;
		
		@media (max-width: 540px) {
			font-size: 16px;
			line-height: 19px;
		}
	}
}