.header {
	
	&_top-content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 30px;
		border-bottom: 1px solid #DEDEDE;
		
		@media (max-width: 540px) {
			display: none;
		}
	}
	
	&_city-block {
		
		svg {
			display: inline-block;
			margin-right: 10px;
		}
	}
	
	&_city {
		display: inline-block;
		font-size: 20px;
		line-height: 24px;
		color: #3B56D7;
		border-bottom: 1px dashed #C4C4C4;
		margin-right: 7px;
	}
	
	&_city-all {
		display: inline-block;
		font-size: 16px;
		line-height: 19px;
		color: #B4B4B4;
	}
	
	&_phone-block {
		
	}
	
	&_phone-item {
		position: relative;
		display: inline-block;
		padding-right: 53px;
		padding-left: 43px;
		font-size: 18px;
		line-height: 22px;
		color: #3B56D7;
		
		&:after {
			content: '';
			display: block;
			position: absolute;
			background: #DEDEDE;
			width: 1px;
			right: 0px;
			top: -20px;
			bottom: -20px;
		}
		
		&:first-child {
			padding-left: 0;
			
			@media (max-width: 1300px) {
				display: none;
			}
		}
		
		&:last-child {
			padding: 0 33px 0 42px;
			
			&::after {
				display: none;
			}
		}
	}
	
	&_bottom-content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 22px 20px 22px 35px;
		border-bottom: 1px solid #DEDEDE;
		
		@media (max-width: 1300px) and (min-width: 541px) {
			padding: 17px 30px 22px 30px;
		}
		
		@media (max-width: 540px) {
			padding: 27px 20px 24px 20px;
		}
	} 
	
	&_nav-wrapper {
		display: flex;
		align-items: center;
	}
	
	&_logo {
		display: inline-block;
		margin-right: 75px;
		
		@media (max-width: 1300px) {
			width: 210px;
			margin-right: 20px;
		}
		
		@media (max-width: 540px) {
			margin-right: 0;
		}
		
		svg {
			width: 308px;
			height: 37px;
			
			@media (max-width: 1300px) {
				width: 210px;
			}
			
			@media (max-width: 540px) {
				position: relative;
				top: 1px;
				width: 188px;
				height: 22.5px;
			}
		}
	}
	
	&_nav {
		display: inline-block;
		
		@media (max-width: 1300px) and (min-width: 541px) {
			position: relative;
			bottom: 1px;
		}
		
		@media (max-width: 540px) {
			display: none;
		}
	}
	
	&_nav-item {
		position: relative;
		display: inline-block;
		font-size: 20px;
		line-height: 24px;
		margin-right: 30px;
		transition: 100ms;
		color: $black;
		
		@media (max-width: 1300px) {
			font-size: 16px;
			line-height: 19px;
			margin-right: 20px;
		}
		
		&:last-child {
			margin-right: 0;
		}
		
		&:hover {
			cursor: pointer;
			color: #3B56D7;
			
			&::before {
				content: "";
				display: block;
				width: 100%;
				position: absolute;
				top: -40px;
				border-top: 6px solid #3B56D7;
				
				@media (max-width: 1300px) {
					top: -35px;
				}
			}
		}
	}
	
	&_nav-dropdown {
		
		&:after {
			content: "";
			position: relative;
			bottom: 3px;
			display: block;
			border: solid #8C8C8C;
			border-width: 0 1px 1px 0;
			display: inline-block;
			margin-left: 10px;
			padding: 3px;
			transform: rotate(45deg);
			-webkit-transform: rotate(45deg);
			
			@media (max-width: 1300px) and (min-width: 541px) {
				margin-left: 6px;
			}
		}
		
		&:hover::after {
			bottom: -2px;
			transform: rotate(45deg) scale(-1, -1);
		}
	}
	
	&_search {
		width: 22px;
		position: relative;
		top: 5px;
		
		@media (max-width: 1300px) and (min-width: 541px) {
			width: 18px;
		}
		
		&:hover {
			fill: #3B56D7;
		}
	}
	
	&_button {
		display: block;
		padding: 16px 16px 14px 16px;
		font-size: 20px;
		line-height: 24px;
		color: $white;
		background: $light_green;
		
		@media (max-width: 1300px) and (min-width: 541px) {
			padding: 14px 13px 13px 13px;
			font-size: 18px;
			line-height: 22px;
		}
		
		@media (max-width: 540px) {
			display: none;
		}
	}
	
	&_burger-button {
		
		@media (min-width: 541px) {
			display: none;
		}
		
		div {
			width: 24px;
			height: 3px;
			background-color: #393D3D;
			margin: 4px 0;
			margin-right: 25px;
		}
	}
}