.breadcrumbs {
    font-size: 18px;
    line-height: 22px;
    padding-top: 33px;
	margin-bottom: 18px;
	
	.back-arrow {
		display: none;
		margin-right: 12px;
		
		@media (max-width: 540px) {
			display: inline-block;
		}
		
		svg {
			display: inline-block;
		}
	}
	
	.breadcrubs-group {
		display: inline-block;
	}
	
	a {
		display: inline-block;
		position: relative;
		margin-left: 22px;
		color: $black;
		
		@media (max-width: 540px) {
			display: none;
		}
		
		&:after {
			content: "/";
			position: absolute;
			left: -15px;
			color: $black;
			
			@media (max-width: 540px) {
				left: -11px;
			}
			
			&:hover {
				cursor: default;
			}
		}
		
		&:first-child {
			margin-left: 0;
			
			&:after {
				content: "";
			}
		}
		
		&:last-child {
			opacity: 0.6;
			
			@media (max-width: 540px) {
				display: inline-block;
				margin-left: 0;
			}
			
			&:after {
				content: "/";
				opacity: 1;
				
				@media (max-width: 540px) {
					display: none;
				}
			}
		}
	}
}