.breadcrumbs {
  font-size: 18px;
  line-height: 22px;
  padding-top: 33px;
  margin-bottom: 18px; }
  .breadcrumbs .back-arrow {
    display: none;
    margin-right: 12px; }
    @media (max-width: 540px) {
      .breadcrumbs .back-arrow {
        display: inline-block; } }
    .breadcrumbs .back-arrow svg {
      display: inline-block; }
  .breadcrumbs .breadcrubs-group {
    display: inline-block; }
  .breadcrumbs a {
    display: inline-block;
    position: relative;
    margin-left: 22px;
    color: #141414; }
    @media (max-width: 540px) {
      .breadcrumbs a {
        display: none; } }
    .breadcrumbs a:after {
      content: "/";
      position: absolute;
      left: -15px;
      color: #141414; }
      @media (max-width: 540px) {
        .breadcrumbs a:after {
          left: -11px; } }
      .breadcrumbs a:after:hover {
        cursor: default; }
    .breadcrumbs a:first-child {
      margin-left: 0; }
      .breadcrumbs a:first-child:after {
        content: ""; }
    .breadcrumbs a:last-child {
      opacity: 0.6; }
      @media (max-width: 540px) {
        .breadcrumbs a:last-child {
          display: inline-block;
          margin-left: 0; } }
      .breadcrumbs a:last-child:after {
        content: "/";
        opacity: 1; }
        @media (max-width: 540px) {
          .breadcrumbs a:last-child:after {
            display: none; } }
