*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    font-size: 100%;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
}

body {
    font-family: "ProximaNova", sans-serif;
    overflow-x: hidden;
    color: $black;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    transition: .3s linear;
}

select {
    border: none;
}

select:active,
select:focus {
    outline: none;
}

select:focus > option {
    outline: 0;
}

input:focus,
textarea:focus {
    border: none;
    outline: none;
}

h1, h2, h3, h4 {
    font-weight: 400;
}

h1 {
    font-size: 80px;
    color: $black;
    line-height: 1;
    max-width: 60%;
}

h2 {
    font-size: 56px;
}

h3 {
    font-size: 36px;
}

h4 {
    font-size: 32px;
}

p {
    font-weight: 300;
    font-size: 24px;
    line-height: 1.67;
    margin: 0;
}

.container {
    max-width: 1220px;
    padding: 0 20px;
    margin: 0 auto;
}

// ================================================
// ================== @Media ======================
// ================================================.

@media (min-width: $xl) {
    [am-container] {
        max-width: 1600px;
    }

    h1 {
        font-size: 112px;
        line-height: 1;
    }

    h2 {
        font-size: 64px;
    }

    h3 {
        font-size: 36px;
    }

    h4 {
        font-size: 32px;
    }

    p {
        font-size: 24px;
        line-height: 1.67;
    }
}

@media (max-width: $lg) {
    [am-container] {
        max-width: 936px;
    }

    h1 {
        font-size: 64px;
        max-width: 100%;
        line-height: 1.12;
    }

    h2 {
        font-size: 40px;
    }

    h3, h4 {
        font-size: 24px;
    }

    p {
        font-size: 16px;
        line-height: 1.5;
    }

}

@media (max-width: $md) {
    [am-container] {
        max-width: 680px;
    }

    h1 {
        font-size: 54px;
        line-height: 1.19;
    }

    h2 {
        line-height: 1.4;
    }

    h3, h4 {
        font-size: 24px;
    }
}

@media (max-width: $sm) {
    [am-container] {
        max-width: 100%;
        margin: 0px 24px;
    }

    [am-sticky="block"] {
        position: static;
    }


    h1 {
        font-size: 32px;
        line-height: 1;
        letter-spacing: -0.01em;
        max-width: 320px;
    }

    h2 {
        font-size: 36px;
        line-height: 1.11;
    }

    h3, h4 {
        font-size: 20px;
        line-height: 1.2;
    }
}

@media (max-width: $xs) {
    [am-container] {
        max-width: 100%;
        margin: 0px;
    }
}