.vacencies {
	margin-bottom: 78px;
	
	@media (max-width: 540px) {
		margin-bottom: 40px;
	}
	
	&_title {
		font-weight: bold;
		font-size: 45px;
		line-height: 55px;
		margin-bottom: 44px;
		
		@media (max-width: 540px) {
			font-size: 28px;
			line-height: 34px;
			margin-bottom: 20px;
		}
	}
	
	&_text {
		max-width: 950px;
		margin: 0 auto;
		font-size: 32px;
		line-height: 39px;
		text-align: center;
		margin-bottom: 36px;
		
		@media (max-width: 540px) {
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 27px;
		}
	}
	
	&_button-wrapper {
		text-align: center;
	}
	
	&_button {
		display: inline-block;
		width: auto;
		margin: 0 auto;
		padding: 16px 16px 14px 16px;
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		color: $white;
		background: $light_green;
		margin-bottom: 96px;
		
		@media (max-width: 540px) {
			width: 100%;
			padding: 16px 0 14px 0;
			text-align: center;
			margin-bottom: 49px;
		}
		
		&:hover {
			cursor: pointer;
		}
	}
	
	&_list-title {
		font-weight: bold;
		font-size: 40px;
		line-height: 49px;
		margin-bottom: 23px;
		
		@media (max-width: 540px) {
			font-size: 28px;
			line-height: 34px;
			margin-bottom: 29px;
		}
	}
	
	&_list-items {
		display: grid;
		grid-gap: 20px;
		grid-template-columns: 1fr 1fr 1fr;
		
		@media (max-width: 540px) {
			display: block;
		}
		
		.slick-track {
			margin-bottom: 16px;
		}
		
		.slick-slide {
			outline: 0;
			outline-offset: 0;
			webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		}
		
		.slick-dots {
			margin: 0 auto;
			text-align: center;
			
			li {
				display: inline-block;
				margin-right: 12px;
				width: 8px;
				height: 8px;
				border-radius: 50%;
				border: none;
				font-size: 0;
				background: rgba(196, 196, 196, 0.5);
				
				&.slick-active {
					background: #C4C4C4;
				}
				
				button {
					display: none;
				}
			}
		}
	}
	
	&_list-item {
		
		@media (max-width: 540px) {
			padding: 0 25px;
		}
		
		.img-wrapper {
			position: relative;
			margin-bottom: 16px;
			
			&::after {
				content: "";
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background: #000000;
				opacity: 0.2;
				z-index: 10;
			}
		}
		
		img {
			width: 100%;
			display: block;
			position: relative;
			border-radius: 6px;
			object-fit: cover;
		}
		
		p {
			width: calc(100% + 20px);
			margin-left: -10px;
			font-size: 24px;
			line-height: 29px;
			color: $black;
			text-align: center;
		}
		
		&:hover {
			cursor: pointer;
			
			.img-wrapper::after {
				opacity: 0.1;
			}
			
			p {
				color: #00CA69;
			}
		}
	}
}